import { AddScheduleData } from "./TimerInterface";

const { ccclass, property } = cc._decorator;
/**
 * 
 */
export class Timer {
    /**
     * id
     */
    id: number;
    /**
     * 
     */
    _isValid: boolean = true;
    /**
     * 
     */
    _time: number = 0;
    /**
     * 
     */
    runTime: number = 0;
    /**
     * 0  -1 nn
     */
    loop: number = 0;
    /**
     * 
     */
    loopCount: number = 0;
    /**
     * 
     */
    intervalTime: number = 0;
    /**
     * 
     */
    scopeIntervalTime: string = null;
    /**
     * 
     */
    callback: Function = null;
    /**
     * 
     */
    updateCallback: Function = null;
    /**
     * 
     */
    target: any = null;
    constructor(params?) {
        if (params) {
            for (const key in params) {
                if (Object.prototype.hasOwnProperty.call(params, key)) {
                    this[key] = params[key];
                }
            }
        }
    }
    /**
     * scopeIntervalTime
     */
    get randomTime() {
        if (!this.scopeIntervalTime) {
            return 0;
        }
        let timeList = this.scopeIntervalTime.split("_");
        if (!timeList[0] || !timeList[1]) {
            return 0;
        }
        return this.scopeRandom_float(Number(timeList[0]), Number(timeList[1]));
    }
    /**
     * 
     */
    scopeRandom_float(min: number, max: number): number {
        return min + Math.random() * (max - min);
    }
    /**
     * 
     */
    delete() {
        this._isValid = false;
    }
}

/**
 * 
 * @author xeroo
 */
@ccclass
class TimerMgr extends cc.Component {
    private _timerList: Timer[] = [];
    private _tempTimerList: Timer[] = [];
    private _timerId: number = 0;

    init() {
        // 
        cc.director.getScheduler().scheduleUpdate(this, 99, false);
    }

    update(dt: number) {
        this.timerUpdate(dt);
    }

    private Log(...optionalParams) {
        if (CC_DEBUG) {
            CC_DEBUG && cc.log(...optionalParams);
        }
    }

    async addTimeOut(time, target: any) {
        return new Promise<Timer>((res) => {
            this.addSchedule(res, target, time);
        });
    }

    /**
     * 
     * @param callback  timerData 
     * @param target 
     * @param intervalTime 
     * @param loop  0  -1 nn 0
     * @param updateCallback  call(dt,time) dt:, time:
     */
    addSchedule(callback: (timerData: Timer, dt: number) => void, target: any, intervalTime: string | number, loop?: number, updateCallback?: (timerData: Timer, dt: number) => void): Timer {
        let timerData = null;
        if (typeof intervalTime == "string") {
            timerData = new Timer({ callback: callback, target: target, scopeIntervalTime: intervalTime, loop: loop ? loop : 0, updateCallback: updateCallback });
            timerData._time = timerData.randomTime;
        } else if (typeof intervalTime == "number") {
            timerData = new Timer({ callback: callback, target: target, intervalTime: intervalTime, loop: loop ? loop : 0, updateCallback: updateCallback });
            timerData._time = timerData.intervalTime;
        }
        this.addTimerList(timerData);
        return timerData;
    }

    /**
     * 
     * @param object
     */
    addScheduleByObject(object: AddScheduleData): Timer {
        return this.addSchedule(object.callback, object.target, object.intervalTime, object.loop ? object.loop : 0, object.updateCallback);
    }

    private addTimerList(timerData: Timer) {
        timerData.id = ++this._timerId;
        this._tempTimerList.push(timerData);
    }

    /**
     *  Timer
     * @param timer
     */
    deleteByTimer(timer: Timer) {
        timer && this.deleteById(timer.id);
    }

    /**
     *  TimerID
     * @param id
     */
    deleteById(id: number) {
        for (const timerData of this._timerList) {
            if (timerData.id == id) {
                timerData._isValid = false;
            }
        }
    }

    /**
     * 
     * tips:  deleteTimer  deleteTimerByTimerId
     * @param callback 
     * @param target 
     */
    deleteByCallback(callback: Function, target: any) {
        for (const timerData of this._timerList) {
            if (timerData.callback == callback && timerData.target == target) {
                timerData._isValid = false;
            }
        }
    }

    /**
     * 
     * tips:  deleteTimer  deleteTimerByTimerId
     * @param target 
     */
    deleteByTarget(target: any) {
        for (const timerData of this._timerList) {
            if (timerData.target == target) {
                timerData._isValid = false;
            }
        }
    }

    private timerUpdate(dt) {
        while (this._tempTimerList.length) {
            let timerData = this._tempTimerList.shift();
            this._timerList.push(timerData);
        }

        this._timerList = this._timerList.filter((timerData) => {
            if (!timerData._isValid) {
                return false;
            }
            timerData._time -= dt;
            timerData.runTime += dt;
            if (timerData.updateCallback) {
                timerData.updateCallback.call(timerData.target ? timerData.target : timerData, timerData, dt);
            }
            if (timerData._time <= 0) {
                ++timerData.loopCount;
                if (timerData.loop == 0) {
                    timerData._isValid = false;
                } else if (timerData.loop == -1) {
                    // this.Log("",timerData.id,"--------",timerData.loopCount);
                } else {
                    --timerData.loop;
                    if (timerData.loop == 0) {
                        timerData._isValid = false;
                    }
                    // this.Log("time id: ",timerData.id,"--------",timerData.loop);
                }
                if (timerData.callback) {
                    timerData.callback.call(timerData.target ? timerData.target : timerData, timerData, dt);
                }

                if (timerData.scopeIntervalTime) {
                    timerData._time = timerData.randomTime;
                    // this.Log("time id: ", timerData.id, "--------", timerData.randomTime);
                } else {
                    timerData._time = timerData.intervalTime;
                }
            }
            return true;
        });
    }
}

export const timerMgr = new TimerMgr();
